* {
  box-sizing: border-box;
}

.header-container {
  display: flex;
  align-items: center;
  width: 100%;
  padding-top: 1%;
  padding-right: 2%;
  overflow: hidden;
  position: relative;
}

.header-container > div:first-child {
  display: flex;
  align-items: center;
  flex-shrink: 0;
}

.logo {
  width: 110px;
  aspect-ratio: 16 / 9;
  object-fit: contain;
}

#logo{
  width: 40px;
  margin-left: 9%;
  margin-right: 9%;
}

#logo-text {
  margin-left: 0;
  font-family: "Bond Time";
  font-size: 2.3vw;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6);
}

.header-container > div:first-child p {
  font-size: 1.3rem;
  font-weight: 300;
  margin: 0;
}

.nav-bar {
  display: flex;
  flex: 1;
  justify-content: flex-end;
  align-items: center;
}

.nav-items {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 33.33%;
  justify-content: space-between;
}

.nav-items li {
  white-space: nowrap;
}

.nav-items a {
  text-decoration: none;
  color: black;
  font-weight: 500;
  transition: color 0.3s;
}

li .responsiveness-hide {
  color: #ffffff;
}

.nav-items a:hover {
  color: #ffc107;
}

.mobile-menu-button {
  display: none;
  cursor: pointer;
  margin-top: 2%;
}

.menu-panel {
  position: fixed;
  top: 0;
  right: -100%;
  width: 100%;
  height: 100%;
  background-color: #f1e0d0;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.3);
  transition: right 0.3s ease-in-out;
  padding: 20px;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: left;
}

.menu-panel.open {
  right: 0;
}

.menu-panel ul {
  list-style: none;
  padding: 0;
}

.menu-panel ul li {
  margin: 20px 0;
}

.menu-panel ul li a {
  text-decoration: none;
  color: black;
  font-weight: 300;
  display: flex;
  justify-content: space-between;
  width: 100%;
  border-bottom: solid #117783;
  padding-bottom: 12%;
}

.menu-panel ul p {
  border-bottom: solid #117783;
  padding-bottom: 12%;
}

.menu-panel ul li span {
  margin-left: 40px;
}

@media (min-width: 768px) and (max-width: 1199px) {
  .nav-bar {
    display: none;
  }

  #logo-text {
  margin-left: 0;
  font-family: "Bond Time";
  font-size: 2.8vw;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6);
}
  .mobile-menu-button {
    display: block;
    font-size: 1.3rem;
    font-weight: 300;
    color: black;
    position: absolute;
    top: 0;
    right: 24px;
  }

  .menu-panel ul li a:hover {
    color: #ffc107;
  }

  .menu-panel .close-button {
    text-align: left;
    display: block;
    position: absolute;
    top: 20px;
    right: 20px;
    background: #f1e0d0;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    transition: background 0.3s ease;
  }

  .menu-panel .close-button:hover {
    background: #f1f1f1;
  }
}

@media (max-width: 767px) {
  .nav-bar {
    display: none;
  }

  #logo-text {
  margin-left: 0;
  font-family: "Bond Time";
  font-size: 3.9vw;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6);
}

  .mobile-menu-button {
    display: block;
    font-size: 1.3rem;
    font-weight: 300;
    color: black;
    position: absolute;
    top: 15px;
    right: 24px;
  }

  .menu-panel ul li a:hover {
    color: #ffc107;
  }

  .menu-panel .close-button {
    text-align: left;
    display: block;
    position: absolute;
    top: 20px;
    right: 20px;
    background: #f1e0d0;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    transition: background 0.3s ease;
  }

  .menu-panel .close-button:hover {
    background: #f1f1f1;
  }

  .logo {
    width: 110px;
    margin-right: -14%;
    margin-left: -13%;
    aspect-ratio: 16 / 9;
    object-fit: contain;
  }
}
