.trash-icon {
  transition: all 0.5s ease;
  fill: #fffcf3;
  margin-top: 2%;
}

.trash-icon:hover {
  fill: rgb(75, 75, 75);
}

#favourites-container {
  display: block;
  text-align: left;
}

.flex-display {
  display: flex;
  justify-content: space-between;
}

.event-cards{
  transition: all 0.5s ease;
  display: flex;
    margin-bottom: 2%;
    padding: 2rem;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border-radius: 1rem;
    border: 1px solid rgba(255, 255, 255, 0.3);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    color: rgb(21, 21, 21);
}

.event-cards:hover {
  transform: scale(105%);
}

@media (min-width: 1200px) {
  .event-cards {
    display: flex;
    padding: 2%;
    gap: 2%;
    margin-bottom: 2%;
    border-radius: 2%;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  }

  .event-images {
    aspect-ratio: 16/9;
    width: 30vw;
    border-radius: 12px;
  }

  #favourites-container {
    padding-right: 22%;
  }
}

@media (min-width: 768px) and (max-width: 1199px) {
  .event-cards {
    display: flex;
    padding-top: 2%;
    gap: 2%;
  }

  .event-images {
    aspect-ratio: 16/9;
    width: 30vw;
    border-radius: 12px;
  }

  #favourites-container {
    padding-right: 22%;
  }
}

@media (max-width: 767px) {
  .event-images {
    aspect-ratio: 16/9;
    width: 100%;
    margin: 0;
    border-radius: 12px;
  }

  .trash-icon {
    transition: all 0.5s ease;
    fill: #fffcf3;
    margin-top: 2%;
  }

  #favourites-container {
    padding-right: 18%;
    padding-top: 1%;
  }
}
